import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { IViewDesignerConfiguration, RX_VIEW_MODEL, RxViewDesignerComponent } from '@helix/platform/view/designer';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs';
import { map, pluck, switchMap, tap } from 'rxjs/operators';
import { RxUtilityModalsService } from '@helix/platform/ui-kit';
import {
  IComponentCanDeactivate,
  RX_APPLICATION,
  RxBundleCacheService,
  RxComponentCanDeactivateGuard,
  RxDefinitionNameService,
  RxGlobalCacheService,
  RxPageTitleService,
  RxPreviousStateService
} from '@helix/platform/shared/api';
import { RX_SHELL } from '@helix/platform/view/api';
import { RxShellModel } from './shell-model.service';
import { values } from 'lodash';

@Component({
  selector: 'rx-shell-designer-page',
  templateUrl: './shell-designer-page.component.html',
  styleUrls: ['./shell-designer-page.component.scss'],
  providers: [
    RxShellModel,
    {
      provide: RX_VIEW_MODEL,
      useExisting: RxShellModel
    }
  ]
})
export class RxShellDesignerPageComponent implements OnInit, OnDestroy, IComponentCanDeactivate {
  @ViewChild(RxViewDesignerComponent, { static: false })
  private viewDesignerComponent: RxViewDesignerComponent;

  configuration$: Observable<IViewDesignerConfiguration> = this.activatedRoute.params.pipe(
    pluck('bundleId'),
    switchMap((bundleId) => this.rxGlobalCacheService.getBundleDescriptor(bundleId)),
    tap((bundleDescriptor) => {
      this.rxBundleCacheService.bundleId = bundleDescriptor.id;

      this.rxPageTitleService.set(
        ['Application shell', bundleDescriptor.friendlyName],
        RX_APPLICATION.innovationStudioBundleId
      );
    }),
    map((bundleDescriptor) => ({
      bundleId: bundleDescriptor.id,
      viewDefinitionName: this.rxDefinitionNameService.getDefinitionName(
        bundleDescriptor.id,
        RX_APPLICATION.shellDefinitionName
      ),
      disablePreview: true,
      paletteComponentsPredicate: (descriptor) => values(RX_SHELL.navBar).includes(descriptor.type)
    }))
  );

  constructor(
    private activatedRoute: ActivatedRoute,
    private rxComponentCanDeactivateGuard: RxComponentCanDeactivateGuard,
    private rxUtilityModalsService: RxUtilityModalsService,
    private rxDefinitionNameService: RxDefinitionNameService,
    private rxPageTitleService: RxPageTitleService,
    private rxBundleCacheService: RxBundleCacheService,
    private rxGlobalCacheService: RxGlobalCacheService,
    private rxPreviousStateService: RxPreviousStateService
  ) {}

  ngOnInit(): void {
    this.rxComponentCanDeactivateGuard.setPageComponent(this);
  }

  ngOnDestroy(): void {
    this.rxComponentCanDeactivateGuard.setPageComponent(null);
  }

  canDeactivate(): boolean {
    if (this.viewDesignerComponent) {
      return this.viewDesignerComponent.canDeactivate();
    }

    return true;
  }

  onCloseDesigner(): void {
    this.rxPreviousStateService.goToPrevState();
  }

  confirmDeactivation(): Promise<boolean> {
    return this.rxUtilityModalsService.confirmUnsavedChanges();
  }
}
